.chat-page {
    display: flex;
    flex-direction: column;
    align-items: center;
    min-height: 40vh;
    justify-content: center;
    border-radius: 15px;
    /* 둥근 모서리 설정 */
    background-color: rgba(128, 128, 128, 0.1);
    /* 투명한 회색 배경 */
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
    /* 약간의 그림자 추가 (선택 사항) */
    margin-left: 20%;
    margin-right: 20%;
    margin-top: 15vh;
    margin-bottom: 10px;

}





.history-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    /* 하단 정렬 */
    border-radius: 15px;
    /* 둥근 모서리 설정 */
    background: linear-gradient(to top, #a8edea 0%, #fed6e3 100%);
    box-shadow: 0 4px 8px rgba(255, 255, 255, 0.676);
    /* 약간의 그림자 추가 (선택 사항) */
    padding-bottom: 20px;
    margin-left: 25%;
    margin-right: 25%;
    margin-top: 20px;
    margin-bottom: 10px;

    /* 추가 스타일 */
    display: flex;
    align-items: center;
    justify-content: center;
}

.history-content {
    display: flex;
    justify-content: center;
    /* 수평 가운데 정렬 */
    align-items: center;
    /* 수직 가운데 정렬 (필요 시) */
    flex-direction: column;
    /* 아이템들을 세로로 쌓이게 함 (선택 사항) */
    text-align: center;
    /* 텍스트가 중앙 정렬되도록 설정 */
    min-height: 20vh;
}



.history-content-text {

    display: flex;
    /* Flexbox 사용 */
    justify-content: center;
    /* 수평 가운데 정렬 (필요 시) */
    align-items: center;
    /* 수직 가운데 정렬 */
    height: 100%;
    /* 컨테이너의 높이를 채워야 함 */
    text-align: center;
    /* 텍스트 가운데 정렬 */


    min-height: 10vh;
    margin-top: 20px;
    margin-bottom: 10px;
    color: white;
    /* 텍스트가 잘 보이도록 흰색 설정 */
    font-family: 'Dancing Script', cursive;
    /* Dancing Script 폰트 적용 */
}


.spinner {
    border: 4px solid rgba(0, 0, 0, 0.1);
    width: 70px;
    height: 70px;
    border-radius: 50%;
    border-left-color: rgb(208, 10, 247);
    animation: spin 1s linear infinite;
}

@keyframes spin {
    to {
        transform: rotate(360deg);
    }
}



.whole-chat::after {
    content: "";
    display: block;
    height: 20px;
    /* 원하는 높이만큼 설정하세요 */
}

.input-box {
    display: flex;
    align-items: center;
    /* margin-bottom: 10px; */
    width: 90%;
    /* input-box의 가로 크기 설정 */

}

