.text-input {
    min-height: 130px;
    padding: 10px;
    border-radius: 5px;
    border: 1px solid #ccc;
    margin-right: 10px;
    flex: 1;
    resize: none; /* 사용자가 크기를 조절할 수 없도록 설정 (선택 사항) */
    overflow-y: auto; /* 스크롤 생성을 위해 자동 */
    line-height: 1.5; /* 줄 간격 설정 */
    font-size: 16px; /* 글자 크기 설정 */
    white-space: pre-wrap; /* 줄 바꿈 허용 */
}

 

.button-container {
    display: flex;
    justify-content: space-between;
    /* 버튼을 좌우로 배치 */
    gap: 10px;
    /* 버튼 사이에 간격 추가 */
    height: 40px;
    
}





.submit-button {
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    background-color: #007bff;
    color: white;
    cursor: pointer;
}

.bubble-container {
    width: 82%;
    /* 말풍선 컨테이너의 가로 크기 설정 */
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}


.bubble {
    display: inline-block;
    /* 텍스트 크기에 맞게 조정됨 */
    padding: 10px;
    /* 텍스트와 테두리 사이의 여백 */
    padding-bottom: 8px;
    border: 2px solid black;
    /* 테두리 설정 */
    border-radius: 5px;
    /* 둥근 모서리 설정 */
    background-color: #ffffff;
    /* 배경색 설정 */
    margin-bottom: 10px;
    margin-top: 10px;
}

.input-bubble {
    align-self: flex-end;
    /* 오른쪽 정렬 */
}

.bubble>p {
    margin: 0px;
}

.button-container {
    display: flex;
    justify-content: center;
    gap: 10px;
    margin-top: 20px;
}



.btn-outline-dark {
    margin-left: 10px;
}

.scale-up-hor-center {
    -webkit-animation: scale-up-hor-center 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
    animation: scale-up-hor-center 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
}

